.table {
    margin-top: 77px;
    overflow: auto;

    @media(max-width: 1024px) {
        margin-top: 70px;
    }

    @media(max-width: 620px) {
        margin-top: 50px;
    }

    &__header {
        background-color: $black-3;
        color: white;
    }

    &__colaps {
        background-color: $ochre;
        font-weight: bold;

        td {
            font-weight: bold !important;
        }
    }

    table {
        border-collapse: collapse;
        line-height: 1.1;
        min-width: 1260px;
        margin: 0 auto;

        & th {
            border: 1px solid $ochre;
            width: 14%;
            padding: 31px 15px;
            text-align: left;
            font-style: normal;
            font-weight: bold;
            font-size: 18px;
            line-height: 18px;

            @media(max-width: 1024px) {
                padding: 15px;
            }


            &:first-child {
                width: 39%;
                padding-left: 20px;

                @media(max-width: 1024px) {
                    width: 23%;
                }
            }

            &:not(:first-child) {
                text-align: center;
            }
        }

        & td {
            padding: 18px 15px;
            border: 1px solid $ochre;
            font-style: normal;
            font-weight: 400;
            font-size: 14px;
            line-height: 14px;

            @media(max-width: 1024px) {
                padding: 15px;
            }

            &:first-child {
                padding-left: 20px;
                text-align: left;
            }

            .b-svg {
                margin: 0 auto;
                display: block;
                width: 19px;
                height: 15px;
            }
        }
    }
}